import Layout from "../../../application/Layout";

﻿import DashboardViewModel from '../viewModels/DashboardViewModel';
import React from 'react';
import {
    AppBar,
    Container,
    IconButton,
    Typography,
    Button,
    Toolbar,
    Grid,
    Paper,
    Avatar,
    Divider, Slider
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'
import avatar from '../../../assets/img/1.jpg'
import StarTwoToneIcon from '@material-ui/icons/StarTwoTone';
import LocalHospitalTwoToneIcon from '@material-ui/icons/LocalHospitalTwoTone';
import AirlineSeatFlatTwoToneIcon from '@material-ui/icons/AirlineSeatFlatTwoTone';
import CreditCardTwoToneIcon from '@material-ui/icons/CreditCardTwoTone';
import Title from "../../../application/components/Title";


export default class Statistics extends DashboardViewModel {

    marks = [
        {
            value: 0,
            label: '0',
        },
        {
            value: 30,
            label: '30',
        },
        {
            value: 100,
            label: '100',
        },
    ];

    notify(): React.ReactNode {
        return (
            <Grid item>
                {this.surgery()}
                {this.ranking()}
            </Grid>
        );
    }

    surgery() {
        return (
            <>
                <Title title='Julho/2020' subtitle={'cirurgias realizadas em relação ao mês anterior'}/>
                <Grid container direction={'column'} aria-disabled={'true'}>
                    <Slider
                        style={{marginTop: Layout.box(4).top}}
                        color={'secondary'}
                        defaultValue={[80]}
                        value={80}
                        aria-labelledby='discrete-slider-always'
                        step={10}
                        marks={this.marks}
                        valueLabelDisplay='on'
                    />
                </Grid>
            </>
        )
    }

    ranking() {
        return <>
            <Title title={'Ranking'} subtitle={'métricas de destaque neste mês'}/>
            
            {/* Health care company */}
            <Grid container>
                <Grid item xs={4} sm={2} container alignItems={'center'}>
                    <CreditCardTwoToneIcon color={'action'}/>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        operadora
                    </Typography>
                </Grid>
                <Grid item xs={8}>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        <strong>UNIMED: Confederação Nacional das Cooperativas Médicas</strong>
                    </Typography>
                </Grid>
            </Grid>
            
            {/* medical procedure */}
            <Grid container>
                <Grid item xs={4} sm={2} container alignItems={'center'}>
                    <AirlineSeatFlatTwoToneIcon color={'action'}/>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        procedim.
                    </Typography>
                </Grid>
                <Grid item xs={8}>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        <strong>2.01.04.23-5 Terapia inalatória - por nebulizaçã</strong>
                    </Typography>
                </Grid>
            </Grid>

            {/* hospital */}
            <Grid container>
                <Grid item xs={4} sm={2} container alignItems={'center'}>
                    <LocalHospitalTwoToneIcon color={'action'}/>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        Hospital
                    </Typography>
                </Grid>
                <Grid item xs={8}>
                    <Typography id='discrete-slider-always' variant={'caption'} color={'textPrimary'}>
                        <strong>GISELDA TRIGUEIRO</strong>
                    </Typography>
                </Grid>
            </Grid>
        </>
    }
}

