import CreditCardTwoToneIcon from '@material-ui/icons/CreditCardTwoTone';

﻿import DashboardViewModel from '../viewModels/DashboardViewModel';
import React, {CSSProperties} from 'react';
import {
    Typography,
    Grid,
    Divider, Chip, Button, IconButton
} from '@material-ui/core';
import InsertInvitationTwoToneIcon from '@material-ui/icons/InsertInvitationTwoTone';
import MoreHorizTwoToneIcon from '@material-ui/icons/MoreHorizTwoTone';
import Layout from '../../../application/Layout';
import DateRangeTwoToneIcon from '@material-ui/icons/DateRangeTwoTone';
import Title from "../../../application/components/Title";

export default class TodaySchedule extends DashboardViewModel {

    css = {
        item: {
            marginBottom: Layout.margin.bottom
        } as CSSProperties
    }

    notify(): React.ReactNode {
        return (
            <Grid item xs={12}>
                <Title title={'Agenda'} subtitle={'cirurgias marcadas para hoje'}>
                    <IconButton color={'primary'} size={'medium'} onClick={() => {}}>
                        <DateRangeTwoToneIcon/>
                    </IconButton>
                </Title>

                {/* Health care company */}
                {[
                    this.renderItem(1),
                    this.renderItem(2),
                    this.renderItem(3),
                ]}
            </Grid>
        );
    }

    public renderItem(index: number) {
        return (
            <Grid item key={index} xs={12} style={this.css.item} container>
                <Grid container item xs={3} sm={1} justify={'flex-start'}>
                    <InsertInvitationTwoToneIcon color={'action'}/>
                    <Typography variant={'button'} color={'textPrimary'}>
                        <strong>
                            15:00
                        </strong>
                    </Typography>
                </Grid>
                <Grid container item xs={7} sm={10} direction={'column'}>
                    <Typography variant={'button'} color={'textPrimary'}>
                        JOÃO MARIA DE SOUSA
                    </Typography>
                    <Typography variant={'caption'} color={'textSecondary'}>
                        Hospital Giselda Trigueiro
                    </Typography>
                </Grid>
                <Grid container item xs={2} sm={1} justify={'flex-end'}>
                    <IconButton color={'primary'}>
                        <MoreHorizTwoToneIcon/>
                    </IconButton>
                </Grid>
            </Grid>
        )
    }
}